*{
  box-sizing: border-box;
}

/* general styling */

body{
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: #fff;
  background-color: #111;
  height: 100vh;
  width: 100vw;
  z-index: -10;
}
span{
  color: #ffb400;
}

/* home page styling */

.yellow-bg{
  background-color: #ffc107;
  height: 200%;
  width: 100%;
  position: fixed;
  transform: rotate(-15deg);
  left: -76%;
  z-index: -1;
}
.home-page-content{
  z-index: 1;
  display: flex;
  align-items: center;
  margin-right: 10%;
}
.my-image{
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 30px;
    box-shadow: 0 0 7px rgba(177, 150, 150, 0.9);
    height: calc(100vh - 80px);
    width: 40%;
    margin: 40px;
    left: 40px;
    top: 40px;
    z-index: 11;
}
.my-content{
  width: 60%;
}
.my-content h1{
  font-size: 38px;
  line-height: 52px;
  font-family: Poppins,sans-serif;
}
.my-content p{
  font-size: 16px;
    line-height: 35px;
    margin: 15px 0 28px;
}
.button {
 background-color: transparent;
 color: white;
}
.button-icon {
  align-items: center;
  border-radius: 50%;
  bottom: 0;
  color: #fff;
  display: flex;
  font-size: 19px;
  height: 55px;
  justify-content: center;
  position: absolute;
  right: -1px;
  top: -1px;
  width: 55px;
  background-color: #ffb400;
}
.more-about{
  background-color: transparent;
  color: white;
  border: 1px solid orange;
  font-size: 15px;
  border-radius: 50px;
    font-weight: 600;
    line-height: 1.4;
    outline: none!important;
    overflow: hidden;
    padding: 16px 70px 16px 35px;
    position: relative;
    text-align: center;
    text-decoration: none!important;
    text-overflow: ellipsis;
    text-transform: uppercase;
    transition: all .25s ease-in-out;
    user-select: none;
    vertical-align: middle;
    white-space: nowrap;
    z-index: 1;
}
.more-about:hover{
  background-color: #ffb400;
  color: #fff;
}


/* Home styling for smaller screens */

@media (max-width:600px){
  .home-page-content{
    display: flex;
    flex-direction: column;
  }
  .my-image{
    height: 250px;
    width: 250px;
    border-radius: 50%;
  }
  .my-content{
    width: 100%;
    text-align: center;
  }
  .my-content h1{
    font-size: 30px;
    line-height: 52px;
    font-family: Poppins,sans-serif;
  }
  .yellow-bg{
    display: none;
  }
}

/* header styling */

.header{
  align-items: center;
  display: flex;
  opacity: 1;
  position: fixed;
  right: 30px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  transition: opacity .3s;
  -webkit-transition: opacity .3s;
  z-index: 3;
  z-index: 2;
}
.icon-box{
  align-items: center;
  justify-content: center;
  background: #2b2a2a;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  height: 50px;
  list-style: none;
  margin: 20px 0;
  position: relative;
  transition: .3s;
  width: 50px;
}
.header .icon-box h2 {
  border-radius: 30px;

  color: #fff;
  font-weight: 500;
  height: 50px;
  line-height: 50px;
  margin: 0;
  opacity: 0;
  padding: 0 25px 0 30px;
  position: absolute;
  right: 0;
  text-transform: uppercase;
  top: 0;
  transition: all .3s;
  z-index: 1;
  font-size: 15px;
}
.icon-box:hover{
  background-color: #ffc107;
}
.header .icon-box h2:hover {
  opacity: 1;
  z-index: 1;
  background-color: #ffc107;
  
}
i{
  color: #ddd;
    font-size: 19px;
    transition: .3s;
}

/* header styling in smaller screens*/
@media (max-width:600px){
  .header{
    background: #2b2a2a;
    bottom: 0;
    display: block;
    left: 0;
    padding: 8px;
    right: auto;
    top: auto;
    transform: none;
    width: 100%;
    z-index: 10;
  }
  .icon-menu{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height:2vh;
  }
  .icon-box{
    background: #474545;
  }
  .icon-box i{
    font-size: 15px;
  }
}

/* about styling */

.about-title{
  margin: 0 auto;
  padding: 80px 0;
  position: relative;
  width: 100%;
  text-align: center;
}

.about-title h1{
  color: #fff;
    font-size: 56px;
    font-weight: 900!important;
    margin: 0;
    text-transform: uppercase;
}

.title-bg {
  color: hsla(0,0%,100%,.07);
  font-size: 110px;
  font-weight: 800;
  left: 0;
  letter-spacing: 10px;
  line-height: .7;
  position: absolute;
  right: 0;
  text-transform: uppercase;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.about-section{
  --bs-gutter-x: 1.5rem;
  --bs-gutter-y: 0;
  display: flex;
  justify-content: center;
  margin-left: calc(var(--bs-gutter-x)*-.5);
  margin-right: calc(var(--bs-gutter-x)*-.5);
  margin-top: calc(var(--bs-gutter-y)*-1);
}

.subsection{
  
  margin: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr ;
  font-size: 15px;
  
}
.subsection p{
  font-weight: 600px;
}
.subsection  li{
  list-style: none;
  margin-right: 40px;
  width: 50%;
}
.experience {
  
  margin: 10%;
  border: 1px solid #252525;
  border-radius: 5px;
  padding: 20px 30px 25px 40px;
 
}

.exp h3{
  display: inline-block;
  font-size: 50px;
  font-weight: 700;
  margin: 0;
  color: #ffb400;
}
.experience span{
  font-size: 33px;
  font-weight: 300;
  right: -24px;
  top: 2px;
}


/* about section in smaller screens */
@media (max-width:600px) {
  .about-section{
    display: flex;
    flex-direction: column;
  }
  .subsection{
    width: 100%;
  }
}